.loading-page {
	display: flex;
	/* flex-direction: column; */
	background-color: #fff;
	width: 100%;
	height: 100%;
	align-items: center;
	justify-content: center;
	margin: 0;
}

.loading-logo {
	height: 35%;
	width: 100%;
	animation: remove_logo 2s 11s linear forwards;
}

@keyframes remove_logo {
	to {
		opacity: 0;
	}
}

#coder{
	animation: dash 8s linear forwards;
	stroke-dasharray: 5000;
	stroke-dashoffset: 5000;
}
@keyframes dash {
	to {
		stroke-dashoffset: 10;
	}
}

#colors{
	opacity: 0;
	animation: colorIn 2s 8s ease-in-out forwards;
}

@keyframes colorIn {
	from {opacity:0;}
	to {opacity: 1;}
}