.landing-page {
  display: flex;
  background-color: rgba(255, 255, 255, 0);
  width: 100%;
  height: 100%;
  font-family: "Chalkduster", fantasy;
  align-items: center;
  flex-direction: column;
  overflow: auto;
  margin: 0;
  transition: 1s;
}

.landing-page::-webkit-scrollbar {
  display: none;
}

.lion-canvas {
  margin: 0;
  position:absolute;
  width:100%;
  height:100%;
  overflow:hidden;
  background-color: rgba(255, 255, 255, 0);
  z-index: 1;
  opacity: 0;
  animation: fade_come 4s 0s ease-in-out forwards;
}

@keyframes fade_come {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

.hello-name {
  text-align: center;
  font-size: 25px;
  margin: 30px;
  position: absolute;
  bottom: 250px;
}

.landing-page .social-medias {
  margin: 30px;
  opacity: 0;
  position: absolute;
  bottom: 0px;
  z-index: 99;
}

.landing-page .social-medias .media-logo {
  width: 35px;
  height: 35px;
  margin: 10px;
}

.tooltip {
  font-family: Arial, Helvetica, sans-serif;
}

.scroll-down {
  position: relative;
  display: block;
  text-align: center;
  font-size: 20px;
  z-index: 99;
  text-decoration: none;
  text-shadow: 0;
  width: 13px;
  height: 13px;
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  opacity: 0;
  transform: translate(-50%, 0%) rotate(45deg);
  animation: fade_move_down 3s 0s ease-in-out infinite;
  flex-shrink: 0;
  margin: 50px;
  position: absolute;
  bottom: 150px;
}

@keyframes fade_move_down {
  0%   { transform:translate(0,-10px) rotate(45deg); opacity: 0;  }
  25%  { opacity: 1;  }
  100% { transform:translate(0,10px) rotate(45deg); opacity: 0; }
}

@media (max-width: 768px) {
  .hello-name {
    font-size: 25px;
    margin: 20px;
  }
  .landing-page .social-medias {
    margin: 20px;
  }
  .landing-page .scroll-down {
    margin: 30px;
  }
  .landing-page .social-medias .media-logo {
    width: 25px;
    height: 25px;
    margin: 7px;
  }
}